html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
  color: #666666;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app > section {
  position: relative;
}

.container {
  position: relative;
}

a {
  color: #03c4eb;
  transition: 0.5s;
}

a:hover,
a:active,
a:focus {
  color: #03c5ec;
  outline: none;
  text-decoration: none;
}

p {
  padding: 0;
  margin: 0 0 3rem 0;
}

ul {
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  margin: 0 0 2rem 0;
  padding: 0;
}

.section-title {
  font-size: 2.2rem;
  color: #111;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  margin-top: 5rem;
}

.section-description {
  text-align: center;
  margin-bottom: 4rem;
}

.section-title-divider {
  width: 5rem;
  height: 0.3rem;
  background: #03c4eb;
  margin: 0 auto;
  margin-bottom: 2rem;
}

.bg-source {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
}

.container {
  padding: 3rem;
}

.slide {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}

.slide-up {
  opacity: 1;
  transform: translate3d(0, 0%, 0);
  transition: all 1s ease;
}

.slide-down {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
  transition: all 1s ease;
}

.delay-0 {
  transition-delay: 0s;
}

.delay-1 {
  transition-delay: 0.3s;
}

.delay-2 {
  transition-delay: 0.6s;
}

.delay-3 {
  transition-delay: 0.9s;
}

.delay-4 {
  transition-delay: 1.2s;
}

.delay-5 {
  transition-delay: 1.5s;
}

.delay-6 {
  transition-delay: 1.8s;
}

.delay-7 {
  transition-delay: 2.1s;
}

.delay-8 {
  transition-delay: 2.4s;
}

.delay-9 {
  transition-delay: 2.7s;
}

@media (max-width: 768px) {
  .delay-0 {
    transition-delay: 0.3s;
  }

  .delay-1 {
    transition-delay: 0.6s;
  }

  .delay-2 {
    transition-delay: 0.9s;
  }

  .delay-3 {
    transition-delay: 1.2s;
  }

  .delay-4 {
    transition-delay: 1.5s;
  }

  .delay-5 {
    transition-delay: 1.8s;
  }

  .delay-6 {
    transition-delay: 2.1s;
  }

  .delay-7 {
    transition-delay: 2.4s;
  }

  .delay-8 {
    transition-delay: 2.7s;
  }

  .delay-9 {
    transition-delay: 3s;
  }
}

#main {
  display: table;
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: top center;
}

#main .main-logo {
  margin: auto;
}

#main .main-logo > div {
  margin: auto;
}

#main .main-container {
  background: rgba(0, 0, 0, 0.7);
  display: table-cell;
  margin: 0;
  padding: 2rem;
  text-align: center;
  vertical-align: middle;
}

#main h1 {
  margin: 30px 0 10px 0;
  font-weight: 700;
  line-height: 48px;
  text-transform: uppercase;
  color: #fff;
}

@media (max-width: 768px) {
  #main h1 {
    font-size: 28px;
    line-height: 36px;
  }
}

#main h2 {
  color: #999;
  margin-bottom: 50px;
}

@media (max-width: 768px) {
  #main h2 {
    font-size: 24px;
    line-height: 26px;
    margin-bottom: 30px;
  }
  #main h2 .rotating {
    display: block;
  }
}

#main .rotating > .animated {
  display: inline-block;
}

#main .actions a {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 8px 20px;
  border-radius: 2px;
  transition: 0.5s;
  margin: 10px;
}

#main .btn-get-started {
  background: #03c4eb;
  border: 2px solid #03c4eb;
  color: #fff;
}

#main .btn-get-started:hover {
  background: none;
  border: 2px solid #fff;
  color: #fff;
}

#main .btn-services {
  border: 2px solid #fff;
  color: #fff;
}

#main .btn-services:hover {
  background: #03c4eb;
  border: 2px solid #03c4eb;
}

#header {
  background: #111;
}

#header #logo {
  float: left;
}

#header #logo img {
  padding: 0.6rem;
  margin: 0;
  max-height: 2rem;
}

@media (max-width: 768px) {
  #header #logo h1 {
    font-size: 2.6rem;
  }
  #header #logo img {
    max-height: 1.5rem;
  }

  #header .nav-menu a {
    padding: 0.5rem;
  }
}

.is-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100; /* this is optional and should be different for every project */
}

#header.is-sticky {
  background: rgba(0, 0, 0, 0.85);
}
#header .nav-container {
  vertical-align: middle;
}

#header .nav-container:after {
  clear: both;
  display: table;
  content: "";
}

/* Nav Menu Essentials */
.nav-menu,
.nav-menu * {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav-menu ul {
  position: absolute;
  display: none;
  top: 100%;
  left: 0;
  z-index: 99;
}

.nav-menu li {
  position: relative;
  white-space: nowrap;
}

.nav-menu > li {
  float: left;
}

.nav-menu li:hover > ul,
.nav-menu li.sfHover > ul {
  display: block;
}

.nav-menu ul ul {
  top: 0;
  left: 100%;
}

.nav-menu ul li {
  min-width: 18rem;
}

/* Nav Meu Container */
#nav-menu-container {
  float: right;
  margin: 0.5rem 0;
}

/* @media (max-width: 768px) {
  #nav-menu-container {
    display: none;
  }
} */

/* Nav Meu Styling */
.nav-menu a {
  padding: 0.8rem 1rem;
  text-decoration: none;
  display: inline-block;
  color: #fff;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-size: 1rem;
  outline: none;
}

.nav-menu a:hover,
.nav-menu li:hover > a {
  color: #18d26e;
}

.nav-menu .menu-active > a {
  color: #03c4eb;
}

.nav-menu ul {
  margin: 0.4rem 0 0 1.5rem;
  box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.08);
}

.nav-menu ul li {
  background: #fff;
  border-top: 1px solid #f4f4f4;
}

.nav-menu ul li:first-child {
  border-top: 0;
}

.nav-menu ul li:hover {
  background: #f6f6f6;
}

.nav-menu ul li a {
  color: #333;
}

.nav-menu ul ul {
  margin: 0;
}

/* Mobile Nav Toggle */
#mobile-nav-toggle {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 999;
  margin: 2rem 2rem 0 0;
  border: 0;
  background: none;
  font-size: 2.4rem;
  display: none;
  transition: all 0.4s;
  outline: none;
}

#mobile-nav-toggle i {
  color: #fff;
}

@media (max-width: 768px) {
  #mobile-nav-toggle {
    display: inline;
  }
}

.btn-pill {
  position: relative;
  margin: 0 0.5rem 0.5rem 0;
  outline: none;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  border: 1px solid rgba(22, 76, 167, 0.6);
  border-radius: 2rem;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.02, 0.01, 0.47, 1);
}

.btn-pill.small {
  padding: 0 0.5rem;
}

.btn-pill.medium {
  padding: 0 1rem;
  font-size: 0.9rem;
}

.btn-pill span {
  color: #164ca7;
  font-size: 0.8rem;
  font-weight: 500;
  letter-spacing: 0.7px;
}
.btn-pill:hover {
  -webkit-animation: rotate 0.7s ease-in-out both;
          animation: rotate 0.7s ease-in-out both;
}
.btn-pill:hover span {
  -webkit-animation: storm 0.7s ease-in-out both;
          animation: storm 0.7s ease-in-out both;
  -webkit-animation-delay: 0.06s;
          animation-delay: 0.06s;
}

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
  25% {
    transform: rotate(3deg) translate3d(0, 0, 0);
  }
  50% {
    transform: rotate(-3deg) translate3d(0, 0, 0);
  }
  75% {
    transform: rotate(1deg) translate3d(0, 0, 0);
  }
  100% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
  25% {
    transform: rotate(3deg) translate3d(0, 0, 0);
  }
  50% {
    transform: rotate(-3deg) translate3d(0, 0, 0);
  }
  75% {
    transform: rotate(1deg) translate3d(0, 0, 0);
  }
  100% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
}
@-webkit-keyframes storm {
  0% {
    transform: translate3d(0, 0, 0) translateZ(0);
  }
  25% {
    transform: translate3d(4px, 0, 0) translateZ(0);
  }
  50% {
    transform: translate3d(-3px, 0, 0) translateZ(0);
  }
  75% {
    transform: translate3d(2px, 0, 0) translateZ(0);
  }
  100% {
    transform: translate3d(0, 0, 0) translateZ(0);
  }
}
@keyframes storm {
  0% {
    transform: translate3d(0, 0, 0) translateZ(0);
  }
  25% {
    transform: translate3d(4px, 0, 0) translateZ(0);
  }
  50% {
    transform: translate3d(-3px, 0, 0) translateZ(0);
  }
  75% {
    transform: translate3d(2px, 0, 0) translateZ(0);
  }
  100% {
    transform: translate3d(0, 0, 0) translateZ(0);
  }
}
.btn-pill:before,
.btn-pill:after {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  background: #1d89ff;
  content: "";
  opacity: 0;
  transition: transform 0.15s cubic-bezier(0.02, 0.01, 0.47, 1),
    opacity 0.15s cubic-bezier(0.02, 0.01, 0.47, 1);
  z-index: -1;
  transform: translate(100%, -25%) translate3d(0, 0, 0);
}
.btn-pill:hover:before,
.btn-pill:hover:after {
  opacity: 0.15;
  transition: transform 0.2s cubic-bezier(0.02, 0.01, 0.47, 1),
    opacity 0.2s cubic-bezier(0.02, 0.01, 0.47, 1);
}
.btn-pill:hover:before {
  transform: translate3d(50%, 0, 0) scale(0.9);
}
.btn-pill:hover:after {
  transform: translate(50%, 0) scale(1.1);
}

